body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.App {
  padding-top: 20px;
  width: 400px;
  margin: auto;
  text-align: center; }
  .App .red {
    color: red; }
  .App img.image {
    display: block;
    width: 60px;
    height: 60px;
    margin: auto; }
    .App img.image:hover {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg); }
  .App ul {
    width: 100%;
    padding: 0;
    margin: 0; }
    .App ul.flex-container {
      margin-bottom: 30px;
      display: flex;
      justify-content: space-between; }
      .App ul.flex-container input {
        flex-grow: 1; }
    .App ul li {
      list-style: none;
      text-align: left; }
    .App ul .btn {
      outline: none;
      padding: 0;
      width: 50px;
      height: 28px;
      border: none;
      font-size: 12px;
      cursor: pointer;
      border-radius: 3px;
      color: #fff; }
    .App ul .btn-default {
      background: #61dafb; }
    .App ul .btn-danger {
      background: #d9534f; }
  .App .item-container {
    border-bottom: 1px dashed;
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    height: 30px; }
    .App .item-container .little-date {
      font-size: 10px;
      color: #ccc; }
    .App .item-container .item-title {
      margin-right: 10px; }
    .App .item-container .item-content {
      font-weight: bolder; }
  .App .item-container button {
    margin-left: 10px; }
  .App .item-container li {
    max-width: 347px;
    overflow: hidden; }

